body {
  background-color: #ffffff;
}
.page__body {
  margin: 0;
  padding: 0;
}
.main-header {
  position: relative;
  background-color: #ffffff;
  width: 320px;
  min-height: 60px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-header {
    width: 768px;
    min-height: 80px;
  }
}
@media (min-width: 1440px) {
