  font-family: "Montserrat", "Arial", sans-serif;
  font-family: "Montserrat";
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #333333;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  box-sizing: border-box;
  word-wrap: normal;
  min-height: 60px;
  display: grid;
  justify-items: center;
  align-items: center;
  background: #ffffff;
  min-width: 85px;
  max-width: 120px;
  word-wrap: break-word;
